<template>
  <div class="pic">
    <div class="left">
      <img :src="img_url" alt="">
    </div>
    <div class="right">
      <div>
        <img :src="result_url" alt="" id="img">
      </div>
      <Button class="download-btn" @click="upload">下载</Button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "pictures",
        props: ['img_url', 'result_url', 'pic_name'],
        methods: {
            downloadImg() {

            },
            upload() {
                console.log(111);
                const image = new Image();
                let name = this.pic_name
                // 解决跨域 canvas 污染问题
                image.setAttribute("crossOrigin", "anonymous");
                image.onload = function () {
                    const canvas = document.createElement("canvas");
                    canvas.width = image.width;
                    canvas.height = image.height;
                    const context = canvas.getContext("2d");
                    context.drawImage(image, 0, 0, image.width, image.height);
                    //得到图片的base64编码数据
                    const url = canvas.toDataURL("image/png");
                    // 生成一个 a 标签
                    const a = document.createElement("a");
                    // 创建一个点击事件
                    const event = new MouseEvent("click");
                    // 将 a 的 download 属性设置为我们想要下载的图片的名称，若 name 不存在则使用'图片'作为默认名称
                    a.download = 'result_' + name || "图片";
                    // 将生成的 URL 设置为 a.href 属性
                    a.href = url;
                    // 触发 a 的点击事件
                    a.dispatchEvent(event);
                    // return a;
                };
                image.src = document.getElementById("img").src;
            }
        }
    }
</script>

<style scoped lang="less">
  .pic {
    overflow: hidden;
    width: 70%;
    margin-left: 15%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 12px 0px rgba(153, 153, 153, 0.2);
    margin-bottom: 200px;

    > div {
      width: 50%;
      padding: 20px;
      float: left;
      text-align: center;

      img {
        width: 100%;
      }

      .download-btn {
        width: 80px;
        height: 34px;
        background: rgba(74, 202, 133, 1);
        border-radius: 4px;
        font-size: 16px;
        display: block;
        background: #4aca85;
        color: #fff;
        border: none;
        margin: 20px auto 0;
      }
    }

    .right {
      > div {
        width: 80%;
        background-image: url('../assets/img/transparent_bg.png');
        font-size: 0;
        margin: 0 auto;
      }

      img {
        width: 100%;
      }
    }
  }
</style>



