<template>
  <div class="index">
    <div class="banner">
      <img src="../assets/img/banner.png" alt="" class="banner-img">
      <div class="banner-content">
        <div class="logo">
          <!--          <img src="../assets/img/logo.png" alt="">-->
          <div class="logo-text">
            <i class="iconfont icon-logo"></i>
            <span>|&nbsp;&nbsp;智能设计</span>
          </div>
          <Button type="default" ghost class="login-btn" @click="modalData.showModal = true" v-if="!this.loginUserData">登录</Button>
          <span v-else class="login-btn">{{this.loginUserData}}</span>
        </div>
        <div class="text">
          <h1>一键去除图片背景</h1>
          <p>100%全自动&nbsp;-&nbsp;5秒出图&nbsp;-&nbsp;免费</p>
          <div class="upload">
            <Button class="upload-one" @click="clickUpload">上传一张图片</Button>
            <span>or</span>
            <span @click="goBatchDeal">批量上传</span>
          </div>
          <input type="file" ref="uploadImg" @change="uploadImg" style="display: none"
                 accept="image/jpeg,image/jpg,image/png">
        </div>
      </div>
    </div>
    <div class="wrapper" :style="result_url.length>0?'top:800px':'top:600px'">
      <pictures v-show="result_url.length>0" :img_url="img_url" :result_url="result_url" :pic_name="org_pic_name[0]"></pictures>
      <div class="carousel">
        <Carousel v-model="value1" loop radius-dot dots="outside">
          <CarouselItem>
            <div class="demo-carousel">1</div>
          </CarouselItem>
          <CarouselItem>
            <div class="demo-carousel">2</div>
          </CarouselItem>
          <CarouselItem>
            <div class="demo-carousel">3</div>
          </CarouselItem>
          <CarouselItem>
            <div class="demo-carousel">4</div>
          </CarouselItem>
        </Carousel>
      </div>
      <div class="advantages">
        <div v-for="(item,index) in advantages">
          <i class="iconfont" :class="item.icon"/>
          <p class="title">{{item.title}}</p>
          <p class="intro">{{item.intro}}</p>
        </div>
      </div>
      <Company></Company>
      <common-footer></common-footer>
    </div>
    <login-modal :modalData="modalData" @loginData = "login"></login-modal>
  </div>
</template>

<script>
    import Company from './company'
    import commonFooter from './commonFooter'
    import LoginModal from './loginModal/loginModal'
    import pictures from "./pictures";
    import {uuid} from '../assets/js/uuid'

    export default {
        name: 'index',
        components: {Company, commonFooter, LoginModal, pictures},
        data() {
            return {
                value1: 0,
                advantages: [
                    {
                        icon: 'icon-tupian',
                        title: '个人和专业',
                        intro: '无论你是平面设计师、摄影师还是自拍爱好者。移除背景从来都不是一件容易的事。'
                    },
                    {
                        icon: 'icon-shijian',
                        title: '省时省力省钱',
                        intro: '一遍又一遍把前景内容和背景分开？现在有更好的办法了，而且是免费的！'
                    },
                    {
                        icon: 'icon-moshubang',
                        title: '100%全自动',
                        intro: '除了图像文件之外，智能设计无需选择像素，无需标记，什么都不需要。'
                    }
                ],
                modalData: {
                    showModal: false
                },
                picIndex: 0, //当前的图片索引，用来命名，从0开始计数
                org_pic: [], //objname数组
                org_pic_name: [], //图片在客户系统中的原名字
                error_pic_name: [], //上传失败的图片原名
                userData: {}, //游客信息
                loginUserData: '',//登录的用户名
                pic_id_list: [],
                task_id: '',
                result_url: '', //当前提交返回的处理过图片的url
                img_url: '',//当前提交返回的图片的url
                pic_name: ''//图片名字
            }
        },
        methods: {
            clickUpload(e) {
                this.$refs.uploadImg.click()
            },
            goBatchDeal () {
                if(this.loginUserData){
                    this.$router.push('/batchDeal')
                }else{
                    this.modalData.showModal = true
                }
            },
            uploadImg(e) {
                this.$Spin.show();
                let file = e.target.files[0]
                this.org_pic_name = []
                this.org_pic_name.push(file.name)
                let OSS = require('ali-oss');
                let client = new OSS({
                    region: 'oss-cn-hangzhou',
                    accessKeyId: 'LTAI4VIH8rP2cjtg',
                    accessKeySecret: 'uzXZC3TB4egoT5FZe0nYn1BsoqXo5x',
                    endpoint: 'oss-cn-hangzhou.aliyuncs.com',
                    bucket: 'deepdraw-test'
                });
                let tempCheckpoint;
                this.multipartUpload(client, file, tempCheckpoint);
            },
            async multipartUpload(client, file, tempCheckpoint) {
                try {
                    let storeDir = uuid();
                    let storeAs = `input_folder/undefined/${storeDir}/${this.picIndex}.jpg`
                    let self = this
                    this.org_pic = []
                    this.org_pic.push(storeAs)
                    let result = await client.multipartUpload(storeAs, file, {}).then(function (result) {
                        console.log(result, this)
                        self.picIndex++
                        if (result.res.requestUrls.length) {
                            self.dealTask()
                        }
                    })
                } catch (e) {
                    console.log(e);
                }
            },
            getTouristInfo() {
                this.axios({
                    url: 'http://47.102.96.81:8000/api/v1/tourist/',
                    method: 'post',
                    data: {},
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then(res => {
                    this.userData = res.data
                    console.log('用户信息',  this.userData);
                })
            },
            // 提交任务
            putTask(formData) {
                this.axios({
                    url: 'http://47.102.96.81:8000/api/v1/put_task/',
                    method: 'post',
                    data: formData,
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then(res => {
                    console.log('提交任务', res.data)
                    this.pic_id_list = res.data.pic_id_list
                    this.task_id = res.data.task_id
                    let param = {
                        user_id: this.userData.id,
                        org_pic: this.org_pic,//[]
                        token: this.userData.token,
                        pic_id_list: this.pic_id_list
                    }
                    this.startTask(param)
                })
            },
            // 开始任务
            startTask(param){
                this.axios.post('http://47.102.96.81:8000/api/v1/start_task/',
                    param).then(res => {
                    console.log('开始任务', res.data)
                    let picParam = {
                        user_id: this.userData.id,
                        token: this.userData.token,
                        pic_id: this.pic_id_list[0]
                    }
                    console.log('picParam', picParam)
                    this.getPic(picParam)
                })
            },
            // 获取图片返回的信息
            getPic(param){
                this.axios.post('http://47.102.96.81:8000/api/v1/get_pic/',
                param).then(res=>{
                    console.log('图片数据', res.data)
                    let data = res.data.result[0]
                    this.img_url = data.org_pic_url
                    this.result_url = data.result_pic_url
                    this.pic_name = data.pic_name
                    this.$Spin.hide();
                    console.log(33333, this.result_url)
                })
            },
            // 提交任务和开始任务调用
            dealTask() {
                let param = {
                    user_id: this.userData.id,
                    task_status: '1',
                    org_pic: this.org_pic,//[]
                    token: this.userData.token,
                    org_pic_name: this.org_pic_name,//[]
                    error_pic_name: []
                }

                console.log('传递参数', param)
                let formData = new FormData()
                Object.keys(param).forEach((key)=>{
                    formData.append(key, param[key])
                })

               this.putTask(formData);
            },
            login (data){
                if(data || sessionStorage.getItem('username')){
                    this.loginUserData = sessionStorage.getItem('username')
                }
                console.log('iiiiiii', this.loginUserData)
            }
        },
        mounted() {
            this.login()
            this.getTouristInfo()
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .banner {
    position: relative;

    .banner-img {
      width: 100%;
      min-height: 600px
    }

    .banner-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;

      .logo {
        padding: 0 250px;

        .icon-logo {
          font-size: 50px;
          color: #fff;
        }

        .logo-text {
          font-size: 20px;
          color: #fff;
          display: inline-block;

          span {
            transform: translateY(-15px);
            display: inline-block;
          }
        }
        .login-btn {
          float: right;
          width: 80px;
          margin-top: 25px;
          color: #fff;
        }
      }

      .text {
        font-size: 16px;
        color: #fff;
        margin-top: 180px;
        text-align: center;

        h1 {
          font-size: 50px;
          font-weight: 300;
        }

        p {
          font-size: 20px;
          margin: 15px 0 50px 0;
        }

        .upload-one {
          width: 130px;
          height: 40px;
          /*line-height: 40px;*/
          display: inline-block;
          background: #4aca85;
          border-radius: 20px;
          color: #fff;
          border: none;
          font-size: 16px;
        }

        .upload {
          font-size: 16px;

          span:nth-child(2) {
            display: inline-block;
            margin: 0 20px;
          }

          span:nth-child(3) {
            display: inline-block;
            border-bottom: 1px solid #fff;
            cursor: pointer;
          }
        }
      }
    }
  }
  .wrapper{
    position: absolute;
    top: 600px;
  }
  .carousel {
    width: 70%;
    margin-left: 15%;
    /* position: absolute; */
    /* overflow: hidden; */
    max-width: 1330px;
    .demo-carousel {
      height: 900px;
      background: antiquewhite;
      color: #fff;
      font-size: 50px;
    }
  }

  .advantages {
    width: 70%;
    margin: 200px 15% 0;
    text-align: center;

    > div {
      width: 33.3%;
      display: inline-block;
      padding: 0 25px;

      .iconfont {
        color: #798EEA;
        font-size: 40px;
      }

      .title {
        font-size: 36px;
        font-weight: 400;
        color: rgba(85, 85, 85, 1);
        margin: 25px 0;
      }

      .intro {
        font-size: 18px;
        font-family: SourceHanSansCN-Regular;
        font-weight: 400;
        color: rgba(153, 153, 153, 1);
      }
    }
  }
</style>



