import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import batchDeal  from "../components/batchDeal";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },{
    path: '/batchDeal',
      name: 'batchDeal',
      component: batchDeal
    }
  ]
})



