<template>
    <Modal v-model="modalData.showModal" class="login-modal" width="50%">
      <div class="text">
        <p>特约商户登录</p>
        <p>即可获得如下权益</p>
        <ul class="ad-list">
          <li>一键抠图，100%全自动</li>
          <li>素材图批量上传</li>
          <li>透明图批量下载</li>
        </ul>
        <p class="special"><i class="iconfont icon-dianhua"></i>想成为特约商户？</p>
        <p>请咨询客服：400-220-620</p>
      </div>
      <div class="login-box">
        <p class="title">商家登录</p>
        <Form :model="loginForm" ref="loginForm" :rules="ruleValidate">
          <FormItem prop="phone">
            <Input v-model="loginForm.phone" placeholder="请输入手机号"></Input>
          </FormItem>
          <FormItem class="login-code" prop="code">
            <Input v-model="loginForm.code" placeholder="请输入验证码" class=""></Input>
            <Button type="primary" @click="clickGetCode" :class="showTime?'time':'get-code'" :disabled="showTime">{{showTime?timer:'获取验证码'}}</Button>
          </FormItem>
          <FormItem>
            <Button type="primary" class="login-btn" @click="login">登录</Button>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
      </div>
    </Modal>
</template>

<script>
    export default {
        name: "loginModal",
        props: ['modalData'],
        data(){
            const validatePhone = (rule, value, callback)=>{
                let reg = /^1[34578]\d{9}$/
                if (!value) {
                    return callback(new Error('手机号不能为空'));
                }else if(!reg.test(value)){
                    return callback(new Error('手机号格式不正确'));
                }else{
                    callback()
                }
            }
            return {
                loginForm:{
                    phone: '',
                    code: ''
                },
                ruleValidate: {
                    phone: [
                        {validator: validatePhone, trigger: 'blur' }
                    ],
                    code: [
                        { required: true, message: '验证码不能为空', trigger: 'blur' }
                    ]
                },
                timer: 60,
                showTime: false,
                loginUserData: {
                    id: '',
                    token: '',
                    user_name: ''
                }
            }
        },
        methods:{
            clickGetCode() {
                let reg = /^1[34578]\d{9}$/
                console.log(333, reg.test(this.loginForm.phone))
                if(reg.test(this.loginForm.phone)){
                    this.timer = 60
                    this.showTime = true
                    this.getCode()
                    setInterval(()=>{
                        if(this.timer >0){
                            this.timer --
                        }else{
                            this.showTime = false
                        }
                    },1000)
                }
            },
            getCode() {
                let param = {
                    Phone: this.loginForm.phone
                }
                this.axios.post('http://47.102.96.81:8000/api/v1/msg/',
                    param).then(res=>{
                    this.$Message.info(res.data.msg);
                    console.log('验证码', res.data)
                })
            },
            login() {
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        let param = {
                            user_name: this.loginForm.phone,
                            password: this.loginForm.code
                        }
                        this.axios.post('http://47.102.96.81:8000/api/v1/user/login/',
                            param).then(res=>{
                                if(res.data){
                                    this.$Message.info(res.data.msg);
                                    if(res.data.code === 1001){
                                        this.loginUserData = {
                                            id: res.data.result.id,
                                            token: res.data.token,
                                            user_name: res.data.result.user_name
                                        }
                                        sessionStorage.setItem('username', res.data.result.user_name)
                                        this.$emit('loginData', res.data.result.user_name)
                                        this.$router.push('/batchDeal')
                                    }
                                }
                            console.log('登录', res.data)
                        })
                    }
                })

            }
        },
        watch: {
            'modalData.showModal': function (){
                this.loginForm = {
                    phone: '',
                    code: ''
                }
            }
        }

    }
</script>

<style lang="less">
.login-modal{
  /*width:770px;*/
  /*height:514px;*/
  overflow: hidden;
  .ivu-modal-body{
    background: #f8f8f8;
    -webkit-box-shadow: 0px 2px 20px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 2px 20px 0px rgba(0, 0, 0, 0.1);
    padding: 80px;
    border-radius: 10px;
    .ivu-form{
      padding: 0 20px;
      .ivu-form-item{
        height: 60px;
      }
      .ivu-form-label-left .ivu-form-item-label{
        text-align: center;
      }
      .login-code{
        .ivu-input-wrapper{
          width: 60%;
        }
        .get-code{
          float: right;
          background: #4aca85;
          border: 0;
        }
        .time{
          background: #fff;
          border: 1px solid #ccc;
          float: right;
          width: 80px;
        }
      }
      .login-btn{
        width: 270px;
        height: 40px;
        background: rgba(74,202,133,1);
        border-radius: 4px;
        border: 0;
        font-size: 16px;
      }
    }

  }
  .ivu-modal-footer{
    display: none;
  }
  .text{
    width: 200px;
    font-size:16px;
    font-weight:400;
    color:rgba(85,85,85,1);
    line-height:18px;
    float: left;
    p:nth-child(1){
      font-weight:600;
      line-height:18px;
      margin-bottom: 8px;
    }
    .ad-list{
      margin: 36px 16px 80px;
      li{
        font-size:14px;
        list-style: disc;
        margin-bottom: 20px;
      }
    }
    .special{
      margin-bottom: 13px;
    }

  }
  .login-box{
    width:310px;
    height:354px;
    margin-left: 380px;
    background:rgba(255,255,255,1);
    .title{
      padding: 45px 0;
      text-align: center;
      font-size: 16px;
      font-weight: 600;
    }
  }
}
</style>



